body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: #444444;
  background-color: #ffffff;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}

.button {
  padding: 10px 25px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button:hover {
  background-color: #5eaa2f;
}
.button--special {
  color: #444444;
  background-color: #f2f2f2;
}
.button--special:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: #ebebeb;
}
.button--special:hover {
  background-color: #ebebeb;
}

.section-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 60px;
    line-height: 60px;
  }
}

@media (min-width: 1440px) {
  .page-header {
    position: relative;
  }
}

.main-nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    width: 768px;
    min-height: 109px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    position: absolute;
    left: 0;
    right: 0;
    width: 1440px;
    min-height: 169px;
    padding-left: 110px;
    padding-right: 110px;
    z-index: 10;
  }
}

.main-nav__controls {
  display: flex;
  justify-content: space-between;
  min-height: 65px;
  padding: 10px 20px 9px;
}
@media (min-width: 768px) {
  .main-nav__controls {
    padding: 0;
    border: none;
  }
}

.main-nav__logo-link {
  display: flex;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .main-nav__logo-link {
    align-items: center;
  }
}
.main-nav__logo-link:active {
  opacity: 0.6;
}
.main-nav__logo-link:hover {
  opacity: 0.8;
}

.main-nav__logo-image {
  margin-right: 57px;
}
@media (min-width: 768px) {
  .main-nav__logo-image {
    margin-top: -8px;
    margin-right: 12px;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo-image {
    margin-top: -5px;
  }
}

@media (min-width: 768px) {
  .main-nav__logo-image--mobile {
    display: none;
  }
}

.main-nav__logo-image--tablet {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__logo-image--tablet {
    display: block;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo-image--tablet {
    display: none;
  }
}

.main-nav__logo-image--desktop {
  display: none;
}
@media (min-width: 1440px) {
  .main-nav__logo-image--desktop {
    display: block;
  }
}

.main-nav__logo-text {
  height: auto;
}
@media (min-width: 768px) {
  .main-nav__logo-text {
    margin-top: -4px;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo-text {
    width: 120px;
    margin-top: 0;
  }
}

.main-nav__toggle {
  position: relative;
  width: 33px;
  height: 45px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 8px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__controls {
    border-bottom: 1px solid #ffffff;
  }

  .main-nav--closed .main-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 21px;
    left: 8px;
    width: 25px;
    height: 2px;
    background-color: #000000;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    position: absolute;
    top: 65px;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #ffffff;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__list {
  position: static;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    min-width: 440px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    min-width: 470px;
    max-width: 479px;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--index .main-nav__link {
    color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--index .main-nav__item--current .main-nav__link {
    border-bottom: 2px solid #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--index .main-nav__item:not(.main-nav__item--current) .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 1440px) {
  .main-nav__list--index .main-nav__item:not(.main-nav__item--current) .main-nav__link:hover {
    color: rgba(255, 255, 255, 0.6);
  }
}

.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 66px;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .main-nav__item {
    width: auto;
    margin-left: 30px;
    border: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    margin-left: 0;
  }
}
.main-nav__item:first-child {
  min-height: 67px;
}
@media (max-width: 767px) {
  .main-nav__item:first-child {
    border-top: 1px solid #e6e6e6;
  }
}

.main-nav__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding-top: 8px;
    padding-bottom: 8px;
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    border-bottom: 2px solid transparent;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    padding-top: 6px;
    padding-bottom: 6px;
    font-size: 20px;
    line-height: 30px;
    color: #000000;
  }
}

@media (min-width: 768px) {
  .main-nav__item--current .main-nav__link {
    border-bottom: 2px solid #68b738;
  }
}

@media (min-width: 768px) {
  .main-nav__item:not(.main-nav__item--current) .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__item:not(.main-nav__item--current) .main-nav__link:hover {
    color: rgba(0, 0, 0, 0.6);
  }
}

.page-main {
  flex-grow: 1;
}

.page-title {
  padding-top: 30px;
  padding-bottom: 42px;
  border-top: 1px solid #d9d9d9;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .page-title {
    padding-top: 32px;
    padding-left: 30px;
    padding-right: 30px;
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .page-title {
    padding-top: 188px;
    padding-bottom: 44px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.page-title--index {
  padding: 0;
  background-color: #5eaa2f;
  background-image: url("../img/index-back-mobile.jpg"), linear-gradient(180deg, #5eaa2f 0%, #5eaa2f 63%, #ffffff 63%, #ffffff 100%);
  background-position: right top, center;
  background-repeat: no-repeat, no-repeat;
  border-top: none;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-title--index {
    background-image: url("../img/index-back-mobile@2x.jpg"), linear-gradient(180deg, #5eaa2f 0%, #5eaa2f 63%, #ffffff 63%, #ffffff 100%);
    background-size: 320px auto, 100% auto;
  }
}
@media (min-width: 768px) {
  .page-title--index {
    padding: 0;
    background-color: transparent;
    background-image: none;
  }
}
@media (min-width: 1440px) {
  .page-title--index {
    padding: 0;
    background-color: #5eaa2f;
    background-image: url("../img/index-back-desktop.jpg"), linear-gradient(270deg, #5eaa2f 0%, #5eaa2f 50%, #ffffff 50%, #ffffff 100%);
  }
}
@media (min-width: 1440px) and (min-resolution: 144dpi), (min-width: 1440px) and (min-resolution: 1.5dppx) {
  .page-title--index {
    background-image: url("../img/index-back-desktop@2x.jpg"), linear-gradient(270deg, #5eaa2f 0%, #5eaa2f 50%, #ffffff 50%, #ffffff 100%);
    background-size: 720px auto, 100% auto;
  }
}

.page-title__inner {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: minmax(79px, auto) minmax(38px, auto) minmax(342px, auto);
  align-items: end;
  padding: 20px 20px 0;
  background-image: url("../img/index-can-mobile.png");
  background-position: bottom 43px center;
  background-repeat: no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-title__inner {
    background-image: url("../img/index-can-mobile@2x.png");
    background-size: 280px auto;
  }
}
@media (min-width: 768px) {
  .page-title__inner {
    grid-template-rows: minmax(162px, auto) minmax(68px, auto) minmax(580px, auto);
    align-items: start;
    padding-top: 31px;
    padding-left: 117px;
    padding-right: 30px;
    background-image: url("../img/index-can-tablet.png");
    background-position: bottom center;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .page-title__inner {
    background-image: url("../img/index-can-tablet@2x.png");
    background-size: 708px auto;
  }
}
@media (min-width: 1440px) {
  .page-title__inner {
    grid-template-rows: minmax(120px, auto) minmax(32px, auto) minmax(239px, auto);
    row-gap: 40px;
    min-height: 694px;
    margin-bottom: 82px;
    padding-top: 223px;
    padding-left: 190px;
    padding-right: 110px;
    background-image: url("../img/index-can-desktop.png");
    background-position: right 329px top 140px;
    background-size: 552px 500px;
  }
}
@media (min-width: 1440px) and (min-resolution: 144dpi), (min-width: 1440px) and (min-resolution: 1.5dppx) {
  .page-title__inner {
    background-image: url("../../img/index-can-desktop@2x.png");
  }
}

.page-title__caption {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: normal;
  line-height: 31px;
  color: #000000;
}
@media (min-width: 768px) {
  .page-title__caption {
    font-size: 60px;
    line-height: 60px;
    text-align: start;
  }
}
@media (min-width: 768px) {
  .page-title__caption--program {
    padding-left: 40px;
  }
}
@media (min-width: 1440px) {
  .page-title__caption--program {
    padding-left: 0;
  }
}

.page-title--index .page-title__caption {
  line-height: 36px;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .page-title--index .page-title__caption {
    max-width: 450px;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    color: #000000;
  }
}

.page-title__slogan {
  justify-self: center;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 12px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .page-title__slogan {
    justify-self: start;
    max-width: 450px;
    margin-left: 2px;
    font-size: 20px;
    line-height: 20px;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .page-title__slogan {
    margin-left: 0;
  }
}

.page-title__select-program-link {
  font-size: 14px;
  line-height: 19px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-title__select-program-link {
    justify-self: start;
    margin-left: 4px;
    font-size: 20px;
    line-height: 33px;
  }
}
@media (min-width: 1440px) {
  .page-title__select-program-link {
    margin-left: 0;
  }
}

.diet-plans {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, max-content);
  grid-gap: 20px;
  padding: 20px;
}
@media (min-width: 768px) {
  .diet-plans {
    grid-gap: 30px;
    padding-top: 0;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .diet-plans {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: max-content;
    grid-gap: 80px;
