    text-align: right;
  }
}
@media (min-width: 1440px) {
  .address__street,
.address__city {
    padding-left: 79px;
    text-align: left;
  }
}

.address__street {
  grid-area: s;
}

.address__city {
  grid-area: c;
}

.map {
  width: 100%;
  height: 100%;
  min-height: 362px;
  background-color: #f2f2f2;
  background-image: url("../img/map-mobile.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .map {
    background-image: url("../img/map-mobile@2x.png");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .map {
    min-height: 400px;
    background-image: url("../img/map-tablet.png");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .map {
    background-image: url("../img/map-tablet@2x.png");
    background-size: 768px auto;
  }
}
@media (min-width: 1440px) {
  .map {
    background-image: url("../img/map-desktop.png");
  }
}
@media (min-width: 1440px) and (min-resolution: 144dpi), (min-width: 1440px) and (min-resolution: 1.5dppx) {
  .map {
    background-image: url("../img/map-desktop@2x.png");
    background-size: 1440px auto;
  }
}

.contacts {
  background-color: #f2f2f2;
}

.contacts__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .contacts__inner {
    flex-direction: row;
    justify-content: space-between;
    padding: 56px 30px;
  }
}
@media (min-width: 1440px) {
  .contacts__inner {
    padding: 57px 110px;
  }
}

.contacts__logo-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 90px;
  margin-top: 5px;
  padding: 20px 0;
}
@media (min-width: 768px) {
  .contacts__logo-link {
    justify-content: start;
    min-height: max-content;
    padding: 0;
  }
}
.contacts__logo-link:active {
  opacity: 0.6;
}
.contacts__logo-link:hover {
  opacity: 0.8;
}

.logo-link {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .logo-link {
    border: none;
  }
}

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  max-width: 132px;
  margin: 0 auto;
  padding: 20px 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .socials {
    max-width: 138px;
    padding: 0;
  }
}

.socials__item {
  display: flex;
  align-items: center;
}

.socials__link {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
}
.socials__link--vk {
  width: 23px;
  height: 16px;
}
.socials__link--vk svg {
  width: 100%;
  height: 100%;
  fill: #666666;
}
.socials__link--vk:hover svg {
  fill: #68b738;
}
@media (min-width: 768px) {
  .socials__link--vk {
    width: 29px;
    height: 17px;
  }
}
.socials__link--instagram {
  width: 17px;
  height: 16px;
}
.socials__link--instagram svg {
  width: 100%;
  height: 100%;
  fill: #666666;
}
.socials__link--instagram:hover svg {
  fill: #68b738;
}
@media (min-width: 768px) {
  .socials__link--instagram {
    width: 23px;
    height: 23px;
    margin-left: 3px;
  }
}
.socials__link--facebook {
  width: 26px;
  height: 20px;
}
.socials__link--facebook svg {
  width: 100%;
  height: 100%;
  stroke: #666666;
}
.socials__link--facebook:hover svg {
  stroke: #68b738;
}
@media (min-width: 768px) {
  .socials__link--facebook {
    width: 19px;
    height: 28px;
    margin-right: 14px;
  }
}
@media (min-width: 1440px) {
  .socials__link--facebook {
    margin-right: 12px;
  }
}
.socials__link:active svg {
  opacity: 0.3;
}

.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 21px 0;
  border-top: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .copyright {
    justify-content: end;
    padding: 0;
    border-top: none;
  }
}
.copyright:hover .copyright__logo {
  fill: #68b738;
}
.copyright:active {
  opacity: 0.3;
}

.copyright__text {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 768px) {
  .copyright__text {
    margin-right: 24px;
  }
}
@media (min-width: 1440px) {
  .copyright__text {
    margin-right: 22px;
  }
}

.copyright__logo {
  width: 27px;
  height: 34px;
  fill: #666666;
}